@import url(https://fonts.googleapis.com/css2?family=Bungee&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Audiowide&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Iceland&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

:root {
  --link-color-hover: #5caaad;
}

.navigation {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 0;
  padding: 0.5rem 1rem;

  color: white;
}

.nav__container {
  display: flex;
  justify-content: space-around;
  align-items: center;

  padding: 0;
}

.nav__link {
  list-style: none;
  transition: all 0.4s linear;

  font-family: 'Bungee', cursive;
  font-size: 2rem;
  line-height: 2rem;
}

.nav__link:hover {
  text-shadow: 0 0 0.2rem #fff, 0 0 1rem #5caaad,
    0 0 1.5rem #5caaad, 0 0 2rem #5caaad,
    0 0 2.5rem #5caaad;
  text-shadow: 0 0 0.2rem #fff, 0 0 1rem var(--link-color-hover),
    0 0 1.5rem var(--link-color-hover), 0 0 2rem var(--link-color-hover),
    0 0 2.5rem var(--link-color-hover);
}

@media (max-width: 768px) {
  .navigation {
    padding-top: 1rem;
  }

  .nav__container {
    flex-wrap: wrap;
  }

  .nav__link {
    width: 50%;
    margin-bottom: 1.5rem;
  }

  .nav__link:nth-of-type(n + 3):nth-of-type(-n + 4) {
    margin-bottom: 0;
  }
}

@media (max-width: 480px) {
  .nav__container {
    margin: 0;
  }

  .nav__link {
    width: 40%;

    font-size: 1.5rem;
    margin-bottom: 0.5rem;
  }

  .nav__link:nth-of-type(1),
  .nav__link:nth-of-type(3) {
    margin-left: 5%;

    text-align: left;
  }

  .nav__link:nth-of-type(2),
  .nav__link:nth-of-type(4) {
    margin-right: 5%;

    text-align: right;
  }
}

@media (max-width: 400px) {
  .nav__link {
    width: 48%;
  }

  .nav__link:nth-of-type(1),
  .nav__link:nth-of-type(3) {
    margin-left: 2%;
  }

  .nav__link:nth-of-type(2),
  .nav__link:nth-of-type(4) {
    margin-right: 2%;
  }
}

/* reference to SF-style interface design
https://www.sitepoint.com/14-top-sci-fi-designs-to-inspire-your-next-interface/
https://answers.ea.com/t5/General-Discussion/Our-single-minded-mission-tracker/td-p/6047517
https://graphicriver.net/item/hud-interface-elements-xt2/21617593
*/

:root {
  --box-color-title: #ffffff;
  --box-color-text: #5caaad;
  --box-color-background: rgba(0, 0, 0, 0.3);
  --fadein-duration: 0.4s;
  --slidein-duration: 0.4s;
  --slidein-delay-gap: 0.5s;
  --html-color-background: #e65127;
  --css-color-background: #0c73b8;
  --javascript-color-background: #e7a328;
  --react-color-background: #70cbf2;
  --nodejs-color-background: #739d69;
  --expressjs-color-background: #353535;
  --mysql-color-background: #32738c;
  --threejs-color-background: #049ef4;
}

.skill-html {
  background: #e65127;
  background: var(--html-color-background);
}

.skill-css {
  background: #0c73b8;
  background: var(--css-color-background);
}

.skill-javascript {
  background: #e7a328;
  background: var(--javascript-color-background);
}

.skill-react {
  background: #70cbf2;
  background: var(--react-color-background);
}

.skill-nodejs {
  background: #739d69;
  background: var(--nodejs-color-background);
}

.skill-expressjs {
  background: #353535;
  background: var(--expressjs-color-background);
}

.skill-mysql {
  background: #32738c;
  background: var(--mysql-color-background);
}

.skill-threejs {
  background: #049ef4;
  background: var(--threejs-color-background);
}

.description-box {
  position: absolute;
  top: 20vh;
  left: 60vw;
  right: 5vw;
  max-height: 60vh;
  overflow-y: auto;
  transition: all 0.4s linear;
  border-radius: 0.5rem;
  box-shadow: 0 0 2rem rgba(255, 255, 255, 0.4);

  background: rgba(0, 0, 0, 0.3);

  background: var(--box-color-background);
  font-family: 'Iceland', cursive;
  color: #5caaad;
  color: var(--box-color-text);
  font-size: 1.25rem;
  text-align: left;
  word-wrap: break-word;

  -webkit-animation-name: fadein;

          animation-name: fadein;
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s;
  -webkit-animation-duration: var(--fadein-duration);
          animation-duration: var(--fadein-duration);
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

.description-box::-webkit-scrollbar {
  width: 0.5rem;
}

.description-box::-webkit-scrollbar-thumb {
  border: 2px solid transparent;
  border-radius: 0.5rem;

  background: #5caaad;

  background: var(--box-color-text);
  background-clip: padding-box;
}

.description-box::-webkit-scrollbar-track {
  box-shadow: inset 0 0 0.5rem #5caaad;
  box-shadow: inset 0 0 0.5rem var(--box-color-text);
  border-radius: 0.5rem;

  background: rgba(0, 0, 0, 0.3);

  background: var(--box-color-background);
}

.description__title,
.description__subtitle,
.description__body {
  margin: 0;
  padding: 1.5rem;
  opacity: 0;

  -webkit-animation-name: slidein;

          animation-name: slidein;
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s;
  -webkit-animation-duration: var(--slidein-duration);
          animation-duration: var(--slidein-duration);
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.description__title {
  padding-bottom: 0.5rem;

  font-family: 'Bungee', cursive;
  color: #ffffff;
  color: var(--box-color-title);
  text-shadow: 0 0 0.2rem #ffffff;
  text-shadow: 0 0 0.2rem var(--box-color-title);
  font-size: 2rem;
  line-height: 2rem;
  text-align: center;

  -webkit-animation-delay: calc(0.4s + 0.5s);

          animation-delay: calc(0.4s + 0.5s);

  -webkit-animation-delay: calc(var(--fadein-duration) + var(--slidein-delay-gap));

          animation-delay: calc(var(--fadein-duration) + var(--slidein-delay-gap));
}

.decorative-line {
  width: 100%;
  height: 2px;

  background: linear-gradient(
    to right,
    rgba(0, 0, 0, 0.3) 5%,
    #5caaad 50%,
    rgba(0, 0, 0, 0.3) 95%
  );

  background: linear-gradient(
    to right,
    var(--box-color-background) 5%,
    var(--box-color-text) 50%,
    var(--box-color-background) 95%
  );
}

.description__subtitle {
  padding-top: 0.5rem;

  font-family: 'Bungee', cursive;
  text-shadow: 0 0 0.1rem #5caaad;
  text-shadow: 0 0 0.1rem var(--box-color-text);
  font-size: 1.25rem;
  line-height: 1.25rem;
  text-align: center;

  -webkit-animation-delay: calc(0.4s + 2 * 0.5s);

          animation-delay: calc(0.4s + 2 * 0.5s);

  -webkit-animation-delay: calc(var(--fadein-duration) + 2 * var(--slidein-delay-gap));

          animation-delay: calc(var(--fadein-duration) + 2 * var(--slidein-delay-gap));
}

.description__body {
  padding: 0 0.5rem 1.5rem;

  -webkit-animation-delay: calc(0.4s + 3 * 0.5s);

          animation-delay: calc(0.4s + 3 * 0.5s);

  -webkit-animation-delay: calc(var(--fadein-duration) + 3 * var(--slidein-delay-gap));

          animation-delay: calc(var(--fadein-duration) + 3 * var(--slidein-delay-gap));
}

.description__close-btn {
  all: unset;
  position: absolute;
  top: 0;
  right: 0;

  padding: 0.1rem;
  transition: all 0.4s linear;
  cursor: pointer;

  color: #ffffff;

  color: var(--box-color-title);
}

.description__close-btn:hover {
  color: #5caaad;
  color: var(--box-color-text);
}

.description__close-btn > svg {
  width: 1.5rem;
  height: 1.5rem;
}

.fadeout {
  opacity: 0;
}

@-webkit-keyframes fadein {
  0% {
    opacity: 0.2;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadein {
  0% {
    opacity: 0.2;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes slidein {
  0% {
    opacity: 0;
    transform: translateX(-10px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slidein {
  0% {
    opacity: 0;
    transform: translateX(-10px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@media (max-width: 768px) {
  .description-box {
    left: 5vw;
    right: 5vw;
    max-height: calc(80vh - 5vw);
  }
}

@media (max-width: 480px) {
  .description-box {
    font-size: 1.2rem;
  }

  .description__title {
    padding-top: 1rem;

    font-size: 1.5rem;
  }

  .description__subtitle {
    padding-bottom: 1rem;

    font-size: 1.2rem;
  }
}

.about {
  padding: 0 1rem;
}

.about__username {
  margin: 0;

  font-size: 2rem;
}

.about__status,
.about__intro {
  margin: 0 0 0.5rem;

  font-size: 1.5rem;
}

.about__skills--title {
  margin: 1rem 0 0.5rem;
  padding-top: 0.5rem;
  border-top: 2px solid var(--box-color-text);

  text-align: center;
  font-size: 2rem;
}

.about__skills > .about__skill {
  position: relative;

  margin-bottom: 0.5rem;
  padding: 0.25rem 0.5rem;

  border-radius: 0.5rem;
  font-size: 1.5rem;
  color: white;
}

.skill-background {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;

  height: 2rem;
  border-radius: 0.25rem;
  opacity: 0.8;
}

.about__skill:last-of-type {
  margin: 0;
}

@media (max-width: 960px) {
  .about__skills > .about__skill {
    font-size: 1.2rem;
  }

  .skill-background {
    height: 1.7rem;
  }
}

@media (max-width: 480px) {
  .about {
    padding: 0.5rem 0.5rem 0;
  }

  .about__username {
    font-size: 1.5rem;
  }

  .about__status,
  .about__intro {
    font-size: 1.2rem;
  }

  .about__skills--title {
    font-size: 1.5rem;
  }
}

.portfolio-item {
  position: relative;

  margin-bottom: 1rem;
  padding: 0.5rem 1rem 0;
  transition: all 0.3s linear;
  border-radius: 0.5rem;
}

.portfolio-item:last-of-type {
  margin-bottom: 0;
}

.portfolio-item:hover {
  background: rgba(255, 255, 255, 0.15);
}

.portfolio-item__title {
  width: calc(100% - 4.5rem);
  margin: 0 0 0.5rem;

  font-family: 'Audiowide', cursive;
  color: var(--box-color-title);
  text-shadow: 0 0 0.1rem var(--box-color-title);
  font-size: 1.25rem;
}

.portfolio-item__subtitle {
  margin: 0;

  font-family: 'Audiowide', cursive;
  text-shadow: 0 0 0.1rem var(--box-color-text);
  font-size: 1rem;
}

.portfolio-item__description {
  margin: 0;

  text-shadow: 0 0 0.2rem var(--box-color-text);
}

.portfolio-item__skills {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

  padding: 0.5rem;
}

.portfolio-item__skill {
  margin: 0.3rem;
  padding: 0.2rem 0.5rem;
  border-radius: 0.25rem;
  transition: all 0.4s linear;
  opacity: 0.8;

  color: white;
  text-align: center;
}

.portfolio-item__skill:hover {
  opacity: 1;
}

.portfolio-item__links {
  position: absolute;
  top: 0.5rem;
  right: 1rem;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.portfolio-item__link {
  all: unset;
  display: block;
  margin-left: 0.5rem;
  transition: all 0.4s linear;
  cursor: pointer;
  opacity: 0.9;

  color: white;
}

.portfolio-item__link:hover {
  color: var(--box-color-text);
}

.portfolio-item__link > svg {
  width: 1.5rem;
  height: 1.5rem;
}

@media (max-width: 480px) {
  .portfolio-item {
    padding: 0.5rem 0.5rem 0;
  }

  .portfolio-item__title {
    width: calc(100% - 4rem);

    font-size: 1.1rem;
  }

  .portfolio-item__subtitle {
    font-size: 0.9rem;
  }

  .portfolio-item__links {
    right: 0.5rem;
  }

  .portfolio-item__link > svg {
    width: 1.5rem;
    height: 1.5rem;
  }
}

.contact-info {
  padding: 0 1rem;
}

.contact-info__email--label,
.contact-info__blog--label,
.contact-info__github--label {
  margin: 0;

  font-size: 2rem;
}

.contact-info__email,
.contact-info__blog,
.contact-info__github {
  margin: 0 0 2rem;
  opacity: 0.9;

  font-size: 1.5rem;
  color: white;
}

.contact-info__github {
  margin: 0;
}

.contact-info__blog > a,
.contact-info__github > a {
  all: unset;
  transition: all 0.4s linear;
  cursor: pointer;

  color: white;
}

.contact-info__blog > a:hover,
.contact-info__github > a:hover {
  color: var(--box-color-text);
}

@media (max-width: 480px) {
  .contact-info {
    padding: 0.5rem 0.5rem 0;
  }

  .contact-info__email--label,
  .contact-info__blog--label,
  .contact-info__github--label {
    font-size: 1.5rem;
  }

  .contact-info__email,
  .contact-info__blog,
  .contact-info__github {
    font-size: 1.2rem;
  }
}

.guestbook-form {
  padding: 0 1rem 1rem;

  text-align: right;
}

.guestbook-form > input {
  all: unset;
  display: block;
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 0.5rem;
  padding: 0.3rem;
  border-bottom: 1px solid var(--box-color-text);

  font-family: 'Noto Sans KR', sans-serif;
  font-size: 1rem;
  text-align: left;
}

.guestbook-form > input::-webkit-input-placeholder {
  font-family: 'Iceland', cursive;
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.8);
}

.guestbook-form > input:-ms-input-placeholder {
  font-family: 'Iceland', cursive;
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.8);
}

.guestbook-form > input::-ms-input-placeholder {
  font-family: 'Iceland', cursive;
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.8);
}

.guestbook-form > input::placeholder {
  font-family: 'Iceland', cursive;
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.8);
}

.guestbook-form__submit {
  all: unset;
  padding: 0.2rem 0.5rem;
  border: 2px solid var(--box-color-text);
  border-radius: 0.5rem;
  transition: all 0.3s linear;

  color: var(--box-color-title);
}

.guestbook-form__submit:hover {
  background: var(--box-color-text);
}

.guestbook-list {
  border-top: 2px dotted var(--box-color-text);
  padding-top: 0.5rem;
}

.guestbook-item {
  position: relative;

  margin-bottom: 0.5rem;
  padding: 0.5rem 1rem;
  transition: all 0.3s linear;
  border-radius: 0.5rem;
}

.guestbook-item:last-of-type {
  margin-bottom: 0;
  padding-bottom: 0;
}

.guestbook-item:hover {
  background: rgba(255, 255, 255, 0.15);
}

.guestbook-item__message {
  margin: 0;

  font-size: 1.2rem;
  font-family: 'Noto Sans KR', sans-serif;
  color: var(--box-color-title);
}

.guestbook-item__user {
  margin: 0;

  font-size: 1rem;
  text-align: right;
}

.guestbook-item__registration-info {
  margin: 0;

  font-size: 1.2rem;
  text-align: right;
}

.registration-info__user {
  margin-right: 0.5rem;

  font-family: 'Noto Sans KR', sans-serif;
  font-size: 1rem;
  color: var(--box-color-title);
}

.registration-info__user::before {
  content: '| ';
  color: var(--box-color-text);
}

.registration-info__time::before {
  content: 'at ';
  font-weight: normal;
}

.registration-info__time::after {
  content: ', ';
  font-weight: normal;
}

@media (max-width: 480px) {
  .guestbook-form {
    padding: 0 0.5rem 1rem;
  }

  .guestbook-form > input::-webkit-input-placeholder {
    font-size: 1.1rem;
  }

  .guestbook-form > input:-ms-input-placeholder {
    font-size: 1.1rem;
  }

  .guestbook-form > input::-ms-input-placeholder {
    font-size: 1.1rem;
  }

  .guestbook-form > input::placeholder {
    font-size: 1.1rem;
  }

  .guestbook-item__message {
    font-size: 1rem;
  }

  .guestbook-item__registration-info {
    font-size: 1rem;
  }

  .registration-info__user {
    display: block;
    margin-right: 0;

    font-size: 0.8rem;
  }
}

main {
  position: absolute;
  width: 100%;
}

